import { Component, Host, h, Prop, State } from '@stencil/core';

/**
 * Date component.
 */
@Component({
  tag: 'bs-date',
  styleUrl: 'date.scss',
  shadow: true,
})
export class BsDate {
  @State() isDateValid: boolean = true;

  @State() isLanguageCodeValid: boolean = true;

  /**
   * Raw date string in yyyy-mm-dd format
   */
  @Prop() date: string;

  /**
   * Language's iso code
   */
  @Prop() languageIsoCode: string;

  /**
   * Has white text?
   */
  @Prop() isWhite?: boolean = false;

  private checkDateFormat() {
    const regex = /^\d{4}-(0[1-9]|1[0-2])-(0[1-9]|[1-2]\d|3[0-1])$/;

    this.isDateValid = regex.test(this.date);
  }

  private checkLanguageCodeFormat() {
    const regex = /^[a-z]{2,3}(?:-[A-Z]{2})?$/;

    this.isLanguageCodeValid = regex.test(this.languageIsoCode);
  }

  private renderDate() {
    const date = new Date(this.date);
    const locale: Intl.LocalesArgument = this.isLanguageCodeValid ? this.languageIsoCode : 'en';
    const options: Intl.DateTimeFormatOptions = {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
    };

    return date.toLocaleDateString(locale, options);
  }

  componentWillRender() {
    this.checkDateFormat();
    this.checkLanguageCodeFormat();
  }

  render() {
    return (
      <Host>
        <div class={`date ${this.isWhite ? 'is-white' : ''}`}>
          {this.isDateValid && this.renderDate()}
        </div>
      </Host>
    );
  }
}
